import {mapMutations} from 'vuex';
import req from '../../utils/req.js'
export default {
	data() {
		return {
			good:{}
		}
	},
	// onLoad(options){
	// 	console.log('options',options)
	// },
	async mounted(){
		// 在uniapp中,如果mounted想要获取到路由传参结果,需要通过组件实例this.$mp获取
		console.log('mounted',this)
		const {goodId} = this.$mp.query;
		let good = await req('/getGoodDetail',{goodId});
		this.good= good;
	},
	methods:{
		...mapMutations(['addShopItem']),
		addToCart(){
			uni.showToast({
				title:"添加成功"
			})
			this.addShopItem(this.good);
		}
	}
}
